.card {
  border: 3px solid rgb(204, 204, 204, 0.5);
  padding: 1rem;
  width: 400px;
  margin: 1rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  border-radius: 1rem;
  box-shadow: 1px 1px 5px black;
}

.card img {
  margin-right: 1rem;
}

h3 {
  font-size: 3rem;
}
img {
  width: 6rem;
}
